import Vue from "vue";
import moment from "moment-jalaali";

Vue.filter('momentTDate', function (date) {
    const d = new Date(date);
    return moment(d).locale('fa').format('dddd jYYYY/jMM/jDD');
});

Vue.filter('momentDate', function (date) {
    const d = new Date(date);
    return moment(d).locale('fa').format('jYYYY/jMM/jDD');
});

Vue.filter('momentHour', function (date) {
    //const hours = moment(date).locale('fa').startOf('day').fromNow();
    return moment(date).locale('fa').calendar({
        sameDay: '[امروز] LT',
        lastDay: '[دیروز] LT',
        lastWeek: 'dddd LT',
        sameElse: 'jYYYY/jMM/jDD',
    });
});

Vue.filter('digit', function (number) {
    return Number(Math.round(number)).toLocaleString();
});