// for global LooOut
import cookie from "vue-cookie";
import store from "@/store";
import router from "@/router";
import Vue from "vue";

const LogOut = {
    install(Vue) {
        Vue.prototype.logout = () => {
            cookie.delete('token');
            cookie.delete('profile');
            cookie.delete('permissions');

            store.state.userId= null;
            store.state.profile= null;
            store.state.permissions= null;
            router.push('/').catch(()=>{});
        }
    },
}
Vue.use(LogOut)

const NotiFy = {
    install(Vue) {
        Vue.prototype.notify = (txt,color) => {
            store.state.notify.is = true;
            store.state.notify.text = txt;
            store.state.notify.color = color
        }
    },
}
Vue.use(NotiFy)

const ErrHdl = {
    install(Vue) {
        Vue.prototype.errorHandle = (error,nLout) => {
            if(error.status===401) {
                Vue.prototype.notify('مشکل در احراز هویت','error');
                if(!nLout) {
                    Vue.prototype.logout();
                }
            } else if (error.status===500 || error.status===404) {
                Vue.prototype.notify('مشکل در ارتباط','error');
            } else {
                let msgs = '';
                Object.keys(error.data).forEach(key => {
                    if(typeof error.data[key]=== 'string'){
                        msgs += error.data[key]+'<br/>';
                    } else {
                        for(let item in error.data[key]){
                            msgs += error.data[key][item]+'<br/>';
                        }
                    }
                })
                Vue.prototype.notify(msgs,'error');
            }
        }
    },
}
Vue.use(ErrHdl)
