<template>

  <v-app style="overflow-y:auto;" >
    <router-view></router-view>
    <NotifyComp></NotifyComp>
  </v-app>
</template>

<script>

import NotifyComp from "@/components/Notify";
import axios from "axios";
export default {
  name: 'App',
  created() {
    this.fetchContactUs();
  },

  components: {
    NotifyComp
  },

  data: () => ({
    //
  }),
  methods : {
    fetchContactUs () {
      this.loading = true;
      axios.get(this.$store.state.host + '/website/api/v1/website/contact-us').then(response=>{
        this.$store.state.contactUs = response.data.data;
      }).catch(error=>{
        this.errorHandle(error.response)
      }).finally(()=>{
        this.loading = false;
      })
    }
  }
};
</script>


